html {
    font-size: 10px;
}

.App {
    position: relative;
    filter: brightness(60%);
}

.container {
    position: absolute;
    width: 54%;
    background-color: white;
    top: 20%;
    right: 21%;
    text-align: center;

    h1 {
        font-size: 3rem;
        text-transform: uppercase;
    }

    p {
        font-size: 2rem;
    }

    .form {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;

        .form-group {
            display: flex;
            align-items: baseline;
            width: 46%;
            margin: 1rem 0;
            border-bottom: 1px solid rgb(211, 211, 211);
            padding: 1rem 0;

            label {
                font-size: 1.6rem;
                text-align: left;
                margin-right: 2%;
            }

            input {
                font-size: 1.6rem;
                border: none;
                flex: 1;
                outline: none;
            }
        }

        .rule-wrap {
            width: 96%;
            text-align: left;
            font-size: 1.6rem;
            margin: 2rem 0;

            label {
                margin-left: 2%;
                font-weight: bold;
            }
        }

        .btn-wrap {
            text-align: center;
            margin-bottom: 2rem;

            button {
                padding: 1.4rem 1rem;
                background-color: #ce5628;
                color: white;
                text-transform: uppercase;
                border: none;

                &:hover {
                    cursor: pointer;
                }
            }
        }
    }
}
